/******************
**  DEFAULT CSS  **
******************/

body {
  margin: 0;
  padding: 0;
  user-select: none;
}

.global {
  height: 100vh;
  display: flex;
  flex-direction: column;
  position: relative;
}

.menu {
  position: absolute;
  top: 0;
  right: 0;
}

a {
  text-decoration: none;
}

/**********************
**  END DEFAULT CSS  **
**********************/

@media (min-width: 481px) and (max-width: 767px) {
}

/*****************
  **  MUSIC INFO  **
  *****************/

.musicInfo {
  height: 40vh;
  min-height: 40vh;
  padding-top: 3vh;
  display: flex;
  flex-direction: column;
  background: linear-gradient(#fbe9e7, #fff);
  .wrapper {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .ninetyPercent {
    width: 90%;
    margin: 0 auto 0 auto;
  }
  .ff {
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
  }
  .genre {
    color: rgba(0, 0, 0, 0.54);
    font-size: 2rem;
    font-weight: 400;
    line-height: 1.20588em;
  }
  .artistName {
    flex: 1;
    text-transform: capitalize;
    text-align: center;
    color: rgba(0, 0, 0, 0.87);
    font-size: 1.3rem;
    font-weight: 400;
    line-height: 1.35417em;
  }
  .title {
    text-align: center;
    margin-left: 5px;
    color: rgba(0, 0, 0, 0.87);
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5em;
  }
  .mainActionButton_title {
    color: rgba(0, 0, 0, 0.87);
    font-size: 0.875rem;
    font-weight: 500;
    text-transform: uppercase;
  }
}

/*****************
  ***   BUTTONS  ***
  *****************/

.buttons {
  height: 10vh;
  min-height: 10vh;
  background: linear-gradient(#fff, #f5f5f5);
  display: flex;
  justify-content: center;
  align-items: center;
}

/*****************
  ****   VIDEO   ***
  *****************/

.video {
  height: 44vh;
  min-height: 44vh;
  background-color: #fff;
  display: flex;
  justify-content: center;
  padding-top: 3vh;
}

.video_video_wrapper {
  width: 90%;
}

.video_circularProgress {
  margin-top: 5vh;
}

.video_tracks_wrapper {
  margin-top: 0.5vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media (min-width: 375px) and (max-width: 400px) {
  .video_tracks_wrapper {
    margin-top: 1.1vh;
  }
}

@media (min-width: 401px) {
  .musicInfo_genre {
    font-size: 2.125rem;
  }

  .musicInfo_artistName {
    font-size: 1.7rem;
  }

  .musicInfo_title {
    font-size: 1.2rem;
  }

  .video_tracks_wrapper {
    margin-top: 1.6vh;
  }
}

@media (min-width: 768px) and (max-width: 1023px) {
  .musicInfo {
    height: 35vh;
    min-height: 35vh;
  }

  .musicInfo_genre {
    font-size: 3.2rem;
    font-weight: 500;
  }

  .musicInfo_artistName {
    font-size: 2.5rem;
    font-weight: 400;
  }

  .musicInfo_title {
    font-size: 1.8rem;
    font-weight: 400;
  }

  .video {
    height: 47vh;
    min-height: 47vh;
    padding-top: 5vh;
  }

  .video_tracks_wrapper {
    margin-top: 2vh;
  }
}

@media (min-width: 1024px) {
  .musicInfo_genre {
    font-size: 4rem;
    font-weight: 500;
  }

  .musicInfo_artistName {
    font-size: 3.1rem;
    font-weight: 400;
  }

  .musicInfo_title {
    font-size: 2.3rem;
    font-weight: 400;
  }

  .video_video_wrapper {
    width: 80%;
  }
}

@media (min-width: 1281px) {
  .video_video_wrapper {
    width: 70%;
  }
}
